<!-- 测试查询历史记录 -->
<template>
  <div class="bg">
    <div><button @click="addPhoneState('15811024484', 0)">PhoneState: 15811024484</button></div>
    <div><button @click="addPhoneState('13700001111', 0)">PhoneState: 13700001111</button></div>

    <!-- 出行探针 -->
    <div><button @click="addTravelName('李刚')">出行探针-名字: 李刚</button></div>
    <div><button @click="addTravelName('元芳')">出行探针-名字: 元芳</button></div>

    <div><button @click="addTravelID('360822198609284091')">出行探针-身份证: 360822198609284091</button></div>
    <div><button @click="addTravelID('36082219750711058X')">出行探针-身份证: 36082219750711058X</button></div>

    <div v-for="(history, k) in getHistoryByIndex">
      <span> {{ getName(k) }} : {{ history }} </span>
    </div>

    <!--<div v-for="history in getHistoryByIndex(gconst.SearchType.PHONE_STATE, 0)">-->
      <!--<span> {{ history }} </span>-->
    <!--</div>-->

  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  export default {
    name: 'TestHistory',
    components: {

    },
    props: {

    },
    data () {
      return {

      }
    },
    methods: {
     addPhoneState ( searchKey, index) {
        let searchType = gconst.SearchType.PHONE_STATE
       this.$store.dispatch('history/addHistory', {
         searchKey,
         searchType,
         index
       })
     },

      addTravelName(searchKey) {
        let searchType = gconst.SearchType.LOGISTICS_REALTIME

        this.$store.dispatch('history/addHistory', {
          searchKey,
          searchType,
          index: 0,
        })
      },

      addTravelID(searchKey) {
        let searchType = gconst.SearchType.LOGISTICS_REALTIME

        this.$store.dispatch('history/addHistory', {
          searchKey,
          searchType,
          index: 1,
        })
      },

      getName (searchType) {
       let title = gddmgr.getScreenTitle(searchType)
        return title
      },
    },
    computed: {
      ...mapGetters('history', ['getHistoryByIndex']),
      getRawData() {
        console.log("-------this.rawData:" + JSON.stringify(this.rawData))
        return this.rawData
      }
    },
    created() {

    }
  }
</script>

<style scoped>
  .bg {
    width: 100%;
    height: 100%;
    background: #F0F0F0;
    overflow: auto; /* 垂直滚动条 */
  }

  .root {
    padding: 20px 50px 0px;
  }
</style>



